.round {
  position: relative;
}

.round label {
  cursor: pointer;
  height: 28px;
  width: 28px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}

.round label:after {
  content: "";
  height: 6px;
  opacity: 0;
  width: 12px;
  border-top: none;
  border-bottom: 2px solid #fff;
  border-left: 2px solid #fff;
  border-right: none;
  position: absolute;
  top: 8px;
  left: 7px;
  transform: rotate(-45deg);
}

.round input[type="checkbox"] {
  visibility: hidden;
}

.round input[type="checkbox"]:checked + label {
  background-color: #66bb6a;
  border-color: #66bb6a;
}

.round input[type="checkbox"]:checked + label:after {
  opacity: 1;
}

